import React from 'react';
import '../CSS/home.css'

function Footer() {

    return (
        <div className="footer">
            <div className="container">
                <div className="foot-data ml-2">
                    <div className="row">
                        <div className="col-md-4">
                            <img src={require("../images/footer-logo.png")} className="img-fluid" alt="Image" />
                            <div className="copy-data mt-4">Copyright 2020 @ Taro Proxies<br />All Rights Reserved</div>
                        </div>
                        <div className="col-md-4">
                            <div className="foot-head">Navigation</div>
                            <div className="nav-foot-flex">
                                <div className><a href="#planmenu">Home</a></div>
                                <div className><a href="#Successmenu">Success</a></div>
                                <div className><a href="#planmenu">Plans</a></div>
                                <div className><a href="#FAQmenu">FAQ</a></div>
                                <div className><a href="#Featuresmenu">Features</a></div>
                            </div>
                        </div>
                        <div className="col-md-4">

                            <div className="nav-foot-flex">
                                <div className="foot-head">Important</div>

                                <div className="foot-head">Social</div>
                                <div className>Privacy Policy</div>

                                <div className="social">
                                    <div className="w-20"><img src={require("../images/twiter.png")} className="img-fluid" alt="Image" /></div>
                                    <div className="w-20"><img src={require("../images/social2.png")} className="img-fluid" alt="Image" /></div>
                                </div>
                                <div className>Terms of Services</div>

                            </div>
                        </div>
                        {/* <div className="col-md-2">

                        </div> */}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Footer;
